import { Component, Host, h } from '@stencil/core';

@Component({
	tag: 'o-carousel-item',
	styleUrl: 'o-carousel-item.scss',
	shadow: true
})
export class OCarouselItem {
	render() {
		return (
			<Host>
				<div class={'flex flex-col snap-start snap-always w-full max-h-full overflow-auto justify-center'}>
					<slot></slot>
				</div>
			</Host>
		);
	}
}
